body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
  width: 87%;
}

.header {
  text-align: center;
  padding: 10px 0;
  background-color: #343a40;
  border-radius: 20px;
  margin: 0.5rem;
  margin-bottom: 2rem;
  box-shadow: 0 0 8px 1px rgb(0 0 0 / 55%);
}

.logo {
  font-size: 1.5rem;
  color: white;
  margin: 0;
  padding: 0;
}

.btn {
  color: rgb(0, 0, 0);
  text-decoration: none;
  padding: 4px 8px;
  font-size: 1.4rem;
  border-radius: 5px;
  text-align: center;
  vertical-align: middle;
}

.btn-success {
  background-color: #4caf50;
  color: rgb(255, 255, 255);
}

.btn-success:hover {
  background-color: #36a139;
}

.green-icon {
  width: 2%;
  align-self: baseline;
  margin-top: 2.1px;
}

.issueList {
  box-shadow: 0px 0px 10px 4px gray;
  margin: 0 auto 2rem;
  border-radius: 10px;
  padding: 0;
}

.issueItem {
  margin: 0 auto;
  padding: 0.5rem 1rem;
  border-bottom: lightgray 1px solid;
  display: flex;
  /* align-items: baseline; */
  justify-content: space-between;
}

.issueItem:hover {
  border-radius: 10px;
  box-shadow: 0px 0px 15px #343a40af;
  background-color: #f4f8fa;
  transform: scale(1.02);
  transition: all 300ms ease;
}

.issue-title {
  display: inline-flex;
  justify-content: left;
  align-items: center;
  margin-bottom: 0.5rem;
}

.issue-desc {
  color: #6c757d;
}

.issue-label {
  border-radius: 15px;
  margin: 0 2px;
  padding: 1px 4px 3px;
  word-break: keep-all;
  font-size: 0.9rem;
}


/* comment */

.comment-title {
  font-size: 1.5rem;
}

.issue-body {
  display: flex;
  margin: 3rem auto;
}

.img-fluid {
  max-width: 100%;
  height: auto;
}

.card-title {
  border-bottom: 1px solid rgb(187, 187, 187);
  background-color: #f8f9fa;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  height: 87px;
}

.card-title p {
  color: rgb(108, 117, 125);
  font-size: 1.5rem;
  margin: 0px;
  padding: 1em;
}

.card-body p,
.card-body pre {
  font-size: 1.2rem;
  margin: 0px;
  padding: 1em;
  word-break: break-all;
  white-space: pre-wrap;
}

.card-body img {
  max-width: 100%;
}

.inner-user-avtar {
  display: none;
}


/* Small devices (landscape phones, 576px and up) */
@media (max-width: 768px) {
  .green-icon {
    margin-top: 4px;
  }

  .btn,
  .comment-title,
  .card-body p,
  .card-body pre {
    font-size: 1rem;
  }
  .issue-title,
  .issue-desc {
    font-size: 0.8rem;
  }
  .issue-label {
    font-size: 0.7rem;
  }

  .container {
    width: 97%;
    padding: 2px;
  }
  .outer-user-avtar {
    display: none;
  }
  .inner-user-avtar {
    display: block;
  }
  .card-title {
    display: flex;
    justify-content: left;
    align-items: center;
  }

  .card-title p {
    font-size: 0.8rem;
  }
}

